// Header
.slip__header {
  background-color: var(--primary);
  color: white;
}
.nav__link-logo {
  color: inherit; 
}
.nav__btn {
  color: inherit;
}
.nav__list-link { 
  color: inherit;
}
.btn__contact {
  background-color: white;
  color: var(--primary);
}


// Hero
.slip__hero {
  position: relative;
  background-color: var(--primary);
  overflow: hidden;
  height: calc(100vh - var(--header-mobile-height));
  
  @media only screen and (min-width: 1200px) {
    height: calc(100vh - var(--header-height));
  }
}
.hero__content {
  position: relative;
  height: 60%;
  text-align: center;
  color: white;
  margin: 0 auto;
  display: flex;
  align-items: flex-end;
  z-index: 1;

  @media (min-width: 992px) {
    height: 50%;
  }
}
.hero__heading {
  color: white;
  line-height: 75px;
  width: 850px;
  margin: 0 auto 18px;
}
.hero__subtext {
  line-height: 25px;
  width: 675px;
  margin: 0 auto;
}

.hero__cta {
  margin-top: 50px;
}

.hero__bg {
  position: absolute;
  left: 0;
  bottom: -7px;
  width: 100%;
  height: 40%;
  background: url('../images/people.svg') repeat-x center;
  background-size: cover;
  z-index: 0;

  @media (min-width: 992px) {
    bottom: -44px;
    height: 55%;
  }
}
  




// Works
.slip__works {
  height: 800px;
  position: relative;
  display: flex;
  align-items: center;
  overflow: visible;
}

.works__content {
  .subheading {
      margin-top: 0;
      margin-bottom: 20px;
  }
}

.works__desc {
  width: 50%;
  padding-top: 5rem;
}

.works__img-bg {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;

  img {
      width: auto;
      height: 115%;
  }
}




// Features
.slip__features {
  padding: 5rem 0;
}

.features__content {
  &> .heading {
      font-size: 55px;
      text-align: center;
      margin-bottom: 30px;
  }

  &> .desc {
      text-align: center;
      width: 800px;
      margin: 0 auto 89px;
  }
}

.features {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.feature {
  background-color: white;
  border-radius: 26px;
  box-shadow: 0 0 25px 0 rgba(33, 120, 255, 0.05);
  padding: 22px;

  &:not(:last-of-type) {
      margin-right: 50px;
  }
}

.feature__icon {
  width: 100px;
  height: 100px;
  background-color: #e1efff;
  border-radius: 20px;
  margin-bottom: 33px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.feature__heading {
  font-family: 'AvenirBlack', sans-serif;
  font-size: 45px;
  color: var(--primary);
  margin-bottom: 10px;
}




//  Works1
.slip__works1 {
  padding: 76px 0;
}
.works1__content {
  display: flex;
  align-items: center;
}
.works1__image {
  width: 50%;
  text-align: center;
  &> img {
      width: 380px;
      height: 686px;
      object-fit: contain;
  }
}
.works1__desc {
  width: 50%;
  margin: 0 auto;
}



